import {isDefined} from '../../utils/utils';
import iconMap from '@esteco/soul-core/icons/file-icons/file-icons-map.json';
export class IconMap {

  public static DEFAULT_ICON = 'not-recognized';
  public static ICON_FILES: IconFile[] = iconMap.icons;
  private readonly FILE_ICON_PATH: string;

  constructor(assetsFolder: string) {
    this.FILE_ICON_PATH = `${assetsFolder}/sprite/file-icons.symbol.svg`;
  }

  public getIconPath(icon: string): string {
    return this.getIconByName(icon) || this.getIconByExtension(icon) || this.getDefaultIcon();
  }


  private getIconByExtension(extension: string): string | undefined {
    if(extension?.charAt(0) !== '.'){
      extension = `.${extension}`;
    }
    const icon = IconMap.ICON_FILES.find(iconFile => iconFile.extensions.indexOf(extension?.toLowerCase()) > -1);
    if (isDefined(icon)) {
      return `${this.FILE_ICON_PATH}#${icon.iconName}`;
    }
  }

  private getIconByName(iconName: string): string | undefined {
    const icon = IconMap.ICON_FILES.find(iconFile => iconFile.iconName.toLowerCase() === iconName?.toLowerCase());
    if (isDefined(icon)) {
      return `${this.FILE_ICON_PATH}#${icon.iconName}`;
    }
  }

  private getDefaultIcon(): string {
    return `${this.FILE_ICON_PATH}#${IconMap.DEFAULT_ICON}`;
  }

}

export type IconFile = { iconName: string, extensions: string[] }
